.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 15px;
}

#navName,
#mainNav {
  background-color: #22333b;
  margin-bottom: 0;
  color: #f2f4f3;
}
#mainNav {
  margin-top: 0;
  margin-bottom: 30px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

body {
  background-image: url(/assets/images/connectwork.png);
}

.projectThumbnail,
.featuredThumbnail {
  background-color: #22333b;
  color: #f2f4f3;
  padding: 0px;
  margin-bottom: 15px;
}

.featuredThumbnail :hover {
  background-color: #f2f4f3;
  color: #22333b;
  /* animation: grow 0.25s linear !important;
  transform: scale(1.12) !important; */
}

.featuredImg :hover {
  -webkit-animation: grow 0.25s linear;
          animation: grow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
}

.techUsed {
  text-align: center;
  color: black;
}

.description {
  text-align: left;
}

/* start of navbar css */
.focus {
  text-align: center;
  color: black;
  background-color: #f2f4f3;
}

.focusedProject {
  text-align: center;
  color: black;
}

@media (max-width: 145px) {
  .focus {
    max-width: 30%;
  }
}

.navPill {
  text-align: center;
}

.navPill:hover {
  background-color: #0d3b66;
  -webkit-animation: grow 0.25s linear;
          animation: grow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
}

.fa-arrow-left {
  color: white;
  -webkit-animation: pulse infinite 1s linear;
          animation: pulse infinite 1s linear;
}

.fa-arrow-left:hover {
  -webkit-animation: grow 0.25s linear;
          animation: grow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
  color: #5c9ead;
}

.fa-star {
  -webkit-animation: wiggle infinite 2s linear;
          animation: wiggle infinite 2s linear;
}

.sticky {
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  /* margin-bottom: 60px; */
}
#mainNav {
  text-align: center;
  z-index: 3;
}
/* end of navbar css */

/* start of RenderSingle css */
.singleImage {
  margin-bottom: 5px;
  margin-right: 5px;
}

@media (min-width: 415px) {
  .singleImage {
    float: left;
    max-width: 30%;
  }
}

.resultsHeader {
  font-size: 20px;
  color: gray;
}

.singleText {
  text-align: left;
}

.singleLink {
  font-size: 30px;
  text-align: center;
}

.singleTextBox {
  background-color: #22333b;
  color: #f2f4f3;
  margin-bottom: 30px;
  padding: 5px;
}
/* end of renderSingle css */

#footertext {
  float: none;
  color: grey;
  display: block;
  text-align: center;
  font-size: 16px;
}

footer {
  background-color: rgba(0, 0, 0, 0.8) !important;
  margin: 0;
  height: 25px;
}

/* animations */
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  50% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
  75% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  50% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
  75% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
  25% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

@keyframes wiggle {
  0% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
  25% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}



